import React, { useState } from 'react';
import './../app/header.css';
import Navbar from './Navbar';
import SocialMedia from './SocialMedia';
import DarkMode from './DarkMode';
export default function Header() {
  const [open, setOpen] = useState(false);
  return (
    <header
      className={`transition-[height] duration-200 ease-in header text-center max-h-screen ${
        open ? 'h-[55em]' : 'h-20'
      } lg:w-header lg:h-screen lg:fixed bg-primary dark:bg-dark-primary top-0 left-0 text-white overflow-hidden lg:overflow-y-auto p-4`}>
      <div className="flex items-center">
        <button
          onClick={() => setOpen(!open)}
          className="flex items-center justify-center border border-gray-100 p-1 w-10 h-10 lg:hidden">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor">
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M4 6h16M4 12h8m-8 6h16"
            />
          </svg>
        </button>
        <div className="grow text-2xl font-bold my-1 pb-2 uppercase">Rendika</div>
      </div>
      <div className="my-3">
        <img
          src="https://spesialis1.orthopaedi.fk.unair.ac.id/wp-content/uploads/2021/02/depositphotos_39258143-stock-illustration-businessman-avatar-profile-picture.jpg"
          className="mx-auto rounded-full w-40 h-4w-40 bg-white my-5"
        />
      </div>
      <p className="text-sm">
        Hi, my name is Muhammad Rendika Purnomo and I&apos;m a software engineer. Welcome to my
        personal website!
      </p>
      <SocialMedia />
      <div className="w-full border-t border-gray-300 opacity-50 my-6 dark:opacity-30" />
      <Navbar />
      <div className="w-full border-t border-gray-300 opacity-50 my-6 dark:opacity-30" />
      <DarkMode />
    </header>
  );
}
