import React, { useEffect } from 'react';
import LinkButton from './../components/inputs/LinkButton';
import Gallery from './../components/inputs/Gallery';
import setTitle from '../utils/setTitle';

export const Portfolio = () => {
  useEffect(() => {
    setTitle('Portfolio');
  }, []);
  return (
    <>
      <section className="cta-section flex flex-col justify-around md:flex-row section pt-10 pb-4 bg-gray-50 dark:bg-dark-background">
        <div className="text-center">
          <h1 className="text-4xl font-bold py-2 text-black dark:text-gray-50">Portfolio</h1>
          <p className="py-2 text-secondary dark:text-dark-secondary px-9">
            Welcome to my online portfolio. Lorem ipsum dolor sit amet, consectetuer adipiscing
            elit. Aenean commodo ligula eget dolor. I&apos;m taking on freelance work at the moment.
            Want some help building your software?
          </p>
          <div className="flex justify-center my-4">
            <LinkButton
              text="Hire Me"
              to="mailto:mhd.rendika@gmail.com"
              icon={
                <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24">
                  <path d="M24 0l-6 22-8.129-7.239 7.802-8.234-10.458 7.227-7.215-1.754 24-12zm-15 16.668v7.332l3.258-4.431-3.258-2.901z" />
                </svg>
              }
            />
          </div>
        </div>
      </section>
      <div className="w-full dark:border-t dark:border-gray-300 opacity-50 my-6 dark:opacity-10" />
      <section className="section mt-5">
        <Gallery />
      </section>
    </>
  );
};
