import React from 'react';
import * as Heroicon from 'react-icons/hi';

export default function Heroicons(props) {
  const { name, size = 1, color, className, style, attr, title } = props;
  // eslint-disable-next-line no-unused-vars
  // const Icon = ;
  return React.createElement(Heroicon[name], {
    size: `${size}em`,
    color,
    className,
    style,
    attr,
    title
  });
}
