import { api } from '../configs/api';
import qs from 'qs';

export default {
  getAll: async (params = {}) => {
    params.populate = '*';
    return await api.get('/user-navigation-menus', {
      params,
      paramsSerializer: (params) => {
        return qs.stringify(params, { arrayFormat: 'repeat', encodeValuesOnly: true });
      }
    });
  },
  getOne: async (id) => api.get(`/user-navigation-menus/${id}`).then(({ data }) => data)
};
