import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import navigationMenuApi from '../../api/navigationMenuApi';
import Heroicons from './../../utils/heroicons';

export default function Navbar() {
  const [navigationMenu, setNavigationMenu] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(async () => {
    setLoading(true);
    navigationMenuApi
      .getAll({ filters: { id: { $eq: 1 } } })
      .then((response) => {
        setNavigationMenu(response.data.data[0]?.attributes?.navigation_menus?.data);
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);

  return (
    <div className="flex mx-10 justify-center lg:justify-start">
      <ul>
        {loading ? (
          <div className="flex items-center justify-center space-x-4">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className={`${loading && 'animate-spin'} fill-white`}
              width="24"
              height="24"
              viewBox="0 0 24 24">
              <path d="M14 22c0 1.104-.896 2-2 2s-2-.896-2-2 .896-2 2-2 2 .896 2 2zm-2-22c-1.104 0-2 .896-2 2s.896 2 2 2 2-.896 2-2-.896-2-2-2zm10 10c1.104 0 2 .896 2 2s-.896 2-2.001 2c-1.103 0-1.999-.895-1.999-2s.896-2 2-2zm-20.001 1c.551 0 1.001.449 1.001 1 0 .551-.449 1-1 1s-1-.448-1-1 .448-1 .999-1zm.002-1h-.002c-1.104 0-1.999.896-1.999 2 0 1.105.896 2 2 2h.001c1.104 0 1.999-.896 1.999-2s-.895-2-1.999-2zm16.999-7c1.104 0 2 .896 2 2s-.896 2-2.001 2c-1.103 0-1.999-.895-1.999-2s.896-2 2-2zm0 14c1.104 0 2 .896 2 2s-.896 2-2.001 2c-1.103 0-1.999-.895-1.999-2s.896-2 2-2zm-14-13c.551 0 1 .448 1 1s-.449 1-1.001 1c-.551 0-.999-.448-.999-1s.449-1 1-1zm-.001-1c-1.104 0-1.999.896-1.999 2s.896 2 1.999 2h.001c1.104 0 2-.896 2-2s-.896-2-2.001-2zm.001 15c.551 0 1 .448 1 1s-.449 1-1.001 1c-.551 0-.999-.448-.999-1s.449-1 1-1zm-.001-1c-1.104 0-1.999.896-1.999 2s.896 2 1.999 2h.001c1.104 0 2-.896 2-2s-.896-2-2.001-2z" />
            </svg>
            <span>Loading...</span>
          </div>
        ) : (
          navigationMenu.map(({ id, attributes }) => (
            <li key={id} className="">
              <Link
                to={attributes.link}
                className="flex justify-start items-center py-2 focus:text-gray-700 hover:text-gray-800 text-white dark:text-gray-300 dark:hover:text-gray-100 space-x-3">
                <Heroicons name={attributes.icon} size="1.5" />
                <span className="text-xl font-bold">{attributes.title}</span>
              </Link>
            </li>
          ))
        )}
      </ul>
    </div>
  );
}
