import { Switch } from '@headlessui/react';
import React, { useEffect, useState } from 'react';

export default function DarkMode() {
  const [enabled, setEnabled] = useState(false);
  useState(() => {
    const darkMode = localStorage.getItem('dark-mode');
    if (darkMode && darkMode === 'true') {
      setEnabled(true);
    }
  }, []);

  useEffect(() => {
    if (enabled) {
      window.document.documentElement.classList.add('dark');
      localStorage.setItem('dark-mode', 'true');
    } else {
      window.document.documentElement.classList.remove('dark');
      localStorage.setItem('dark-mode', 'false');
    }
  }, [enabled]);
  return (
    <div className="flex flex-col justify-center items-center font-bold">
      <div className="flex justify-start items-center space-x-2 text-sm">
        <span>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-4 w-h-4"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor">
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z"
            />
          </svg>
        </span>
        <span>Dark Mode</span>
      </div>
      <div>
        <div className="py-5">
          <Switch
            checked={enabled}
            onChange={setEnabled}
            className={`${enabled ? 'bg-primary' : 'bg-primary opacity-50'}
          relative inline-flex flex-shrink-0 h-[28px] w-[64px] border-2 border-gray-200 dark:border-primary dark:bg-dark-primary rounded-full cursor-pointer transition-colors ease-in-out duration-200 focus:outline-none focus-visible:ring-2  focus-visible:ring-white focus-visible:ring-opacity-75`}>
            <span className="sr-only">Use setting</span>
            <span
              aria-hidden="true"
              className={`${enabled ? 'translate-x-9' : 'translate-x-0'}
            pointer-events-none inline-block h-[22px] w-[22px] mt-[0.5px] ml-[0.5px] rounded-full bg-gray-200 shadow-lg transform ring-0 transition ease-in-out duration-200`}
            />
          </Switch>
        </div>
      </div>
    </div>
  );
}
